import React from "react"
import "../style.css"
const givenTemplateKey = "College_Horizon"

async function createWriteKey(templateKey,password,metaData){
    try{
        let res =await fetch("https://exius.nrdlab.org/WriteKey/createWriteKey",{
        headers:{
        'Content-Type': 'application/json',
        authorization:`templateKey:${templateKey};password:${password}`},
        method:"POST",
        body:JSON.stringify({metaData:metaData})})
        return await res.json()}
    catch(e){
        throw e
    }
}        
async function Send_Data_To_Exius(params,templateKey,writeKey){
    // [{endpoint:Horizon_CSV,data:data,fname:fname}]
    try{
        var fd=new FormData()
        for ( const fileInfo of params){
            console.log(fileInfo)
            let URL = new Blob([fileInfo.data], { type: 'text/csv;charset=utf-8;' });
            fd.append(fileInfo.endpoint,URL,fileInfo.fname)
        }
        let res = await fetch("https://exius.nrdlab.org/Upload",{
            headers:{authorization:`templateKey:${templateKey};writeKey:${writeKey}`},
            method:"POST",
            body: fd})
        return await res.json()}
    catch(e){
        throw e
    }}

class Initiate extends(React.Component){
    // This takes in a prop nextInstruction which calls a parent function to move to the next instruction as well as
    // getWriteKey which passes the writeKey to the main HorizonGame.
    constructor(props){
        super(props)
        this.password=""
        this.state={preflightError:false,preflightFileFail:{},preflightInitiated:false}
        this.submitPreflight=this.submitPreflight.bind(this)
        this.updateInputValue=this.updateInputValue.bind(this)
        this.getQuery=this.getQuery.bind(this)
        this.randomIdPreflight = this.randomIdPreflight.bind(this)
        console.log(this.props)
    }
    updateInputValue(event){
        this.password=event.target.value
    }
    async randomIdPreflight(){
        this.submitPreflight(Math.round(Math.random()*10000))
    }
    async submitPreflight(id){
        try{
            this.setState({preflightInitiated:true})
            let writeKey=await createWriteKey(givenTemplateKey,this.password,"test_data")
            let dataPreflight=await Send_Data_To_Exius([{endpoint:"HorizonJSON",fname:`Subject_${id}.json`,data:""},{endpoint:"HorizonCSV",fname:`Subject_${id}.csv`,data:""}],givenTemplateKey,writeKey.writeKey)
            if (Object.keys(dataPreflight.failedFiles).length===0){
                this.props.getWriteKey(writeKey.writeKey,id)
                this.props.nextInstruction()
            }
            else{
                this.setState({preflightFileFail:dataPreflight})
            }
        }
        catch(e){
            console.log("error")
            this.setState({preflightError:true})
        }
    }
    getQuery(){
        return Object.fromEntries([...new URLSearchParams(window.location.search)])
    }
    render(){
        if (!this.state.preflightError && Object.keys(this.state.preflightFileFail).length===0){
            let query_obj = this.getQuery()
            let pwd = (!(query_obj.pwd == null))? "" : "pwd "
            let id = (!(query_obj.id == null))? "" : "id "
            if (pwd+id !== ""){
                return <div> Invalid Essential Query String Parameters :{pwd+id}. Please contact Skyler Wyly at skylerwyly@email.arizona.edu </div>
            }

            if (this.getQuery().pwd){
                this.password=this.getQuery().pwd
                if (!this.state.preflightInitiated){
                    this.submitPreflight(this.getQuery().id)
                }
                else{
                    return <h1 style ={{width:"100%",top:"400px",textAlign:"center",position:"relative"}}>
                    Verifying Credentials...
                </h1>
                }
            }
            else{
                // remove this block for replication study
                    return <div>
                    <h1 style={{top:"200px",position:"relative",textAlign:"center"}}>Input Experiment Password Below</h1>
                    <div style={{display:"flex", justifyContent:"center"}}>
                        <input style={{top:"200px",position:"relative",width:"400px",height:"50px",fontSize:"50px"}} type="text" onChange={this.updateInputValue}></input>
                    </div>
                    <div style={{display:"flex", justifyContent:"center"}}>
                        <button className="fullscreenbutton" style={{width:"200px",height:"400", top:"300px"}} onClick={this.randomIdPreflight}>Submit</button>
                    </div>
                    <h1 style={{width:"100%",top:"400px",textAlign:"center",position:"relative"}}>{(this.state.preflightInitiated)?"Verifying Credentials...":""}</h1>
                    </div>
            }

        }
        if(this.state.preflightError){
            return <div>Credentials Preflight Failed. Please Contact Skyler Wyly at skylerwyly@email.arizona.edu</div>
        }
        else{
            return <div>Failed Files: {this.dataPreflight}. Please Contact Skyler Wyly at skylerwyly@email.arizona.edu</div>
        }
    }

}
export default Initiate